.home {
  height: 100vh;
  width: 100%;
  overflow: hidden;
  background: url("../../../public/images/bg.jpg") center/cover;
  .main-content {
    animation: animate 2s forwards cubic-bezier(0.5, 1, 0.89, 1);

    h1 {
      margin-top: 200px;
      text-align: center;
      font-size: 6em;
      font-weight: bold;
      text-transform: uppercase;
      font-style: italic;
      color: white;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
    }
    h2 {
      text-align: center;
      font-size: 2.7em;
      color: whitesmoke;
      font-style: italic;
      opacity: 0;
      filter: blur(4px);
      animation: fade-in 0.8s 0.1s forwards cubic-bezier(0.11, 0, 0.5, 0);
    }
  }
}
@keyframes animate {
  100% {
    transform: scale(1.1);
  }
}
@keyframes fade-in {
  100% {
    opacity: 1;
    filter: blur(0);
  }
}
